<template>
	<view>
		<view class="code">
			
		</view>
	</view>
</template>

<script>
	var that
	const app = getApp()
	export default {
		data() {
			return {
				id:'',
				info:'',
				rewardedVideoAd:null,
				is_code:false
			}
		},
		methods: { 
			getInfo(){
				uni.request({
					url:app.globalData.url+'group_code/read',
					data:{api_token:app.globalData.api_token,id:that.id},
					success(res) {
						if(res.data.result == -2){
							uni.navigateTo({
								url:'/pages/login/login'
							})
						}
						that.info = res.data.data
						if(that.info.fee == 1){
							if(that.info.is_ad){
								console.log('播放广告')
								if (that.videoAd) {
									
									that.videoAd.show().catch(() => {
								    // 失败重试
								    that.videoAd.load()
								      .then(() => that.videoAd.show())
								      .catch(err => {
								        console.log('激励视频 广告显示失败')
								      })
								  })
								}
							}else{
								that.is_code = true
							}
						}else{
							if(that.info.is_fee){
								that.is_code = true
							}
						}
					}
				})
			},
			check(id){
				uni.navigateTo({
					url:'/pages/chat/detail?id='+id
				})
			}
		},
		onLoad(e) {
			var pages = getCurrentPages()
			var page = pages[pages.length - 1];
			that = this; //是否已登录
			that.id = e.id
			uni.setStorage({
				key: 'url',
				data: '/' + page.route+'?id='+e.id,
				success: function() {
					app.isLogin().then(function() {
						if(uni.createRewardedVideoAd){
							that.rewardedVideoAd = uni.createRewardedVideoAd({ adUnitId: 'adunit-bcacea395ee0c554' })
							that.rewardedVideoAd.onLoad(() => {
								app.checkLogin()
								that.getInfo()
							})
							that.rewardedVideoAd.onError((err) => {
								console.log('onError event emit', err)
							})
							that.rewardedVideoAd.onClose((res) => {
								console.log('onClose event emit', res)
							})
						}
						
					})
				}
			})
			
		}
	}
</script>

<style>
	.code{
		height: 491rpx;
		background: url('https://xcx.huitengzx.com/images/code_bg.png') no-repeat;
		background-size: cover;
		position: relative;
	}
</style>